/**** Mobile layout ****/
@media (max-width: 900px) {
  .udacity-navigation-container, .udacity-title {
    display: none !important;
  }
  .mdl-layout__drawer-button {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .mdl-layout__header-row {
    position: relative;
    justify-content: center;
    padding-left: 72px;
  }
  .udacity-mobile-title {
    display: flex !important;
    position: absolute;
    justify-content: center;
    top: 0.5rem;
    transition: opacity 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  }
  .udacity-mobile-title a {
    height: 100%;
    display: flex;
    justify-content: center;
    text-decoration: none;
  }
  .udacity-mobile-title a img {
    height: 100%;
    max-height: 25%;
  }
  .udacity-mobile-title a h3 {
    color: black;
    margin: 0rem 1rem;
    text-decoration: none;
  }
  /* WebViews in iOS 9 break the "~" operator, and WebViews in OS X 10.10 break
     consecutive "+" operators in some cases. Therefore, we need to use both
     here to cover all the bases. */
  .udacity.udacity-search-box.is-focused ~ .udacity-mobile-title,
  .udacity-search-box.is-focused + .udacity-navigation-container + .udacity-mobile-title {
    opacity: 0;
  }
  .udacity-more-button {
    display: none;
  }
  .udacity-search-box.is-focused {
    width: calc(100% - 48px);
  }
  .udacity-search-box .mdl-textfield__expandable-holder {
    width: 100%;
  }
  .udacity-be-together-section {
    height: 350px;
  }
  .udacity-slogan {
    font-size: 26px;
    margin: 0 16px;
    padding-top: 24px;
  }
  .udacity-sub-slogan {
    font-size: 16px;
    margin: 0 16px;
    padding-top: 8px;
  }
  .udacity-create-character {
    padding-top: 200px;
    font-size: 16px;
  }
  .udacity-create-character img {
    height: 12px;
  }
  .udacity-fab {
    display: none;
  }
  .udacity-wear-band-text {
    margin-left: 0;
    padding: 16px;
  }
  .udacity-footer .mdl-mega-footer--bottom-section {
    display: none;
  }
}

